






import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class CarouselPosts extends Vue {
  private listSentences: string[] = [];
  private nSentences: number = this.listSentences.length;

  @Prop() public msg!: string;

  constructor() {
    super();
    this.listSentences = [
      "Ti Amano tutti, perché hai un cuore grande",
      "I grandi sognatori non dormono mai",
      "Ottimo! Siete aperti al nuovo",
      "Non è vero che ogni verità può andare bene",
      "Custodiamo ciò che la Natura ci ha donato",
      "Il lavoro che mi attende è ancora tutto da fare",
      "Tu sei una persona di quelle che si incontrano quando la vita decide di farti un regalo"
    ];
    this.nSentences = this.listSentences.length;
  }
  created() {
    this.msg = this.listSentences[this.nextSentence()];
  }

  nextSentence(): number {
    return Math.floor(Math.random() * this.nSentences);
  }

  public clickHanlder() {
    this.msg = this.listSentences[this.nextSentence()];
    console.log("click " + this.msg);
  }
}
